  methods: {
    async addWord () {
      if (this.newData.kanji === '' || this.newData.example === '' || this.newData.explain === '') return
      try {
        const response = await fetch('https://script.google.com/macros/s/AKfycbw36boghGfcFqGwuhgySChawTZ1BAVLA8vDe1MfdSaSERMsUMQOfZTTiqc0nnk8SG1M/exec', {
          method: 'POST',
          mode: 'no-cors',
          headers: {
            'Content-Type': 'application/json'
          },
          body: JSON.stringify({ value: this.newData }) // 將資料傳送至 Apps Script
        })
        // const result = await response.text()
        console.log(response)
        this.newData = {
          kanji: '',
          hiragana: '',
          explain: '',
          example: '',
          desc: '',
          isdone: 0
        }
      } catch (error) {
        console.error('Error:', error)
      }
    }

  }
}
