<template>
  <div class="list bg-[#e7b5b5] p-5">
    <div class="z-[999] flex justify-between items-center p-5 bg-[#e7b5b5] fixed top-0 left-0 w-full">
      <div @click="$router.push('/')" class="px-6 py-1 tracking-widest font-bold rounded-md bg-[#8f8f8f] text-white w-fit">戻る</div>
      <label for="show">
        <p class="px-6 py-1 rounded-md text-[#e39696] tracking-widest font-bold  w-fit"  :class="{ 'bg-white': !showHiragana,'bg-[#8f8f8f]': showHiragana, 'text-[#c4c3c3]': showHiragana }" @click="showHiragana = !showHiragana">平假名/説明</p>
      </label>
      <label for="show">
        <p class="px-6 py-1 rounded-md text-[#e39696] tracking-widest font-bold  w-fit"  :class="{ 'bg-white': !filterStatus,'bg-[#8f8f8f]': filterStatus, 'text-[#c4c3c3]': filterStatus }" @click="changeList()">復習</p>
      </label>
    </div>
    <div v-for="(item, index)  in filterList" v-show="!filterStatus ||(filterStatus&&item.isdone === 1)" :key="index" class="card bg-white rounded-md p-1 mb-5 mt-[72px]">
      <div class="relative flex items-start card-title py-2 mb-1 h-full">
        <div class=" w-1/2 h-full flex items-center justify-start card-title-left p-1">
          <div class="bg-[#c77f7f] absolute -top-4 -left-3 w-[30px] h-[30px] text-white rounded-md flex items-center justify-center">{{ index + 1 }} </div>
          <div class="ml-3 flex justify-center items-center w-full h-full">
            <div class="text-2xl text-[#e39696] relative flex justify-center">
              <p class="absolute -top-4 text-xs text-[#e39696] text-nowrap" :class="{ 'invisible opacity-0': !showHiragana }">{{item.hiragana}}</p>
            <p class="font-bold">{{item.kanji}}</p>
          </div>
          </div>
        </div>
        <div class="w-1/2 h-full flex items-center justify-center p-1 relative">
          <div class="text-[#545454]">{{ item.explain }}</div>
          <div class="flex items-center justify-end absolute -top-2 right-0">
            <img src="../../src/assets/images/volume.png" class="mr-2 w-[26px]" @click="synthesizeSpeech(item.kanji)" alt="">
            <img src="../../src/assets/images/fav-click.png" v-if="item.isdone === 1" class="w-[26px]" alt="" @click="isDone(index, 0)">
            <img src="../../src/assets/images/fav.png" v-else class="w-[26px]" alt="" @click="isDone(index, 1)">
          </div>
         </div>
      </div>
      <div class="text-left text-lg p-1 text-[#545454] flex items-start">
        <p class="w-full">例：{{ item.example }}</p>
        <img src="../../src/assets/images/volume.png" class="w-[26px]" @click="synthesizeSpeech(item.example)" alt="">
      </div>
      <div class="text-left p-1 text-[#ff6868]" v-if="item.desc !== ''">*{{ item.desc }}</div>
    </div>
  </div>
  <div class="model" v-if="isLoading">
    <div class="loader"></div>
  </div>
</template>

<script>
// @ is an alias to /src
// import HelloWorld from '@/components/HelloWorld.vue'
import axios from 'axios'

export default {
  name: 'SuccessView',
  components: {
    // HelloWorld
  },
  data () {
    return {
      list: [],
      filterList: [],
      isLoading: false,
      showHiragana: false,
      filterStatus: false,
      freeLimit: 4000000
    }
  },
  created () {
    this.getList()
    if (localStorage.getItem('characterCount')) {
      this.freeLimit = localStorage.getItem('characterCount')
    } else {
      localStorage.setItem('characterCount', this.freeLimit)
    }
    // this.list = [
    //   {
    //     kanji: '団結',
    //     hiragana: 'だんけつ',
    //     explain: '團結',
    //     example: 'dsv',
    //     desc: 'dsvdsv',
    //     isdone: 1
    //   },
    //   {
    //     kanji: 'sdvsdv',
    //     hiragana: 'sdv',
    //     explain: 'sdv',
    //     example: 'svd',
    //     desc: 'sdvds',
    //     isdone: 1
    //   },
    //   {
    //     kanji: 'sdvsdv',
    //     hiragana: 'sdv',
    //     explain: 'sdv',
    //     example: 'svd',
    //     desc: 'sdvds',
    //     isdone: 1
    //   }
    // ]
  },
  mounted () {
  },
  methods: {
    async synthesizeSpeech (text) {
      const apiKey = 'AIzaSyDfmP1GO2ynxUd0FRs33tSHtFEN6wd1CF0'
      const url = `https://texttospeech.googleapis.com/v1/text:synthesize?key=${apiKey}`

      const requestBody = {
        input: { text },
        voice: { languageCode: 'ja-JP', ssmlGender: 'NEUTRAL' },
        audioConfig: { audioEncoding: 'MP3' }
      }

      try {
        const response = await axios.post(url, requestBody)
        const audioContent = response.data.audioContent
        const audio = new Audio(`data:audio/mp3;base64,${audioContent}`)
        audio.play()
        this.freeLimit = this.freeLimit - text.length
        localStorage.setItem('characterCount', this.freeLimit)
        if (this.freeLimit < 10000) {
          alert('即將到達每個月上限，請注意!!')
          return
        }
      } catch (error) {
        console.error('Error with Text-to-Speech API:', error)
      }
    },
    goback () {
      this.$router.push('/')
    },
    async getList () {
      this.isLoading = true
      try {
        const response = await axios.get('https://script.google.com/macros/s/AKfycbw36boghGfcFqGwuhgySChawTZ1BAVLA8vDe1MfdSaSERMsUMQOfZTTiqc0nnk8SG1M/exec', {
          headers: {
            'Content-Type': 'application/json'
          }
        })
        console.log(response.data)
        this.list = response.data.list
        this.filterList = this.list
        this.isLoading = false
        console.log(response.list)
      } catch (error) {
        console.error('Error:', error)
      }
    },
    async isDone (index, currentValue) {
      console.log(index, currentValue)
      this.list[index].isdone = currentValue
      try {
        const response = await fetch('https://script.google.com/macros/s/AKfycbw36boghGfcFqGwuhgySChawTZ1BAVLA8vDe1MfdSaSERMsUMQOfZTTiqc0nnk8SG1M/exec', {
          method: 'POST',
          mode: 'no-cors',
          headers: {
            'Content-Type': 'application/json'
          },
          body: JSON.stringify({ value: { row: index, isdone: currentValue } }
          )
        })
        const result = await response.text()
        console.log('71', result)
      } catch (error) {
        console.error('Error:', error)
      }
    },
    changeList () {
      this.filterStatus = !this.filterStatus
      // this.filterList = this.list.filter((item) => {
      //   if (this.filterStatus) {
      //     return item.isdone === 1
      //   } else {
      //     return item
      //   }
      // })
    }
  }
}
</script>
<style lang="scss" scoped>
.list{
  min-height: 100vh;
  .card{
    height: 200px;
    .card-title{
      height: 100px;
      border-bottom: 1px dashed #b1b1b1;
      .card-title-left{
        border-right: 1px dashed #b1b1b1;
      }
    }
  }
}
.model{
  height: 100vh;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 0;
  left: 0;
  background: rgba(0,0,0,0.5);
  .loader {
    border: 5px solid #f3f3f3; /* Light grey */
    border-top: 5px solid #f47189; /* Blue */
    border-radius: 50%;
    width: 60px;
    height: 60px;
    animation: spin 2s linear infinite;
  }

  @keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
}
}
</style>
